import axios from 'axios';

const baseURL = process.env.VUE_APP_BASE_URL;

export function listarUsuarios(codempresa, tipousuario) {
    return axios.get(`${baseURL}usuarios/listar_clientes?tipousuario=${tipousuario}&codempresa=${codempresa}`);
}

export function crearUsuarios(codempresa, tipousuario, usuario, empresa, password, ubigeo, direccion, correo, telefono, domicilio_fiscal, ruc) {
    return axios.post(`${baseURL}usuarios/crear_usuarios`,
        {
            codempresa_cuenta: codempresa,
            tipousuario: tipousuario,
            usuario: usuario,
            password: password,
            empresa: empresa,
            ubigeo: ubigeo,
            direccion: direccion,
            correo: correo,
            telefono: telefono,
            domicilio_fiscal: domicilio_fiscal,
            ruc: ruc
        });
}

export function guardarImagenLogo(logoImage, codempresa) {
    const formData = new FormData();
    console.log("enviara la imagen mediante http")
    formData.append('logoImage', logoImage);
    formData.append('codempresa', codempresa);
    console.log(formData)
    return axios.post(`${baseURL}usuarios/guardarLogo`, formData, {
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    });
}

export function eliminarUsuarioCuenta(idUsuario, idEmpresa, tipousuario) {
    return axios.delete(`${baseURL}usuarios/cuenta_eliminar`,
        {
            data: {
                idUsuario: idUsuario,
                idEmpresa: idEmpresa,
                tipousuario: tipousuario
            }
        });
}

export function buscarUsuarioCuenta(idUsuario, idEmpresa) {
    return axios.get(`${baseURL}usuarios/search?idUsuario=${idUsuario}&idEmpresa=${idEmpresa}`);
}

export function editarUsuarios(id_usuario,id_empresa, usuario, empresa, password, ubigeo, direccion, correo, telefono, domicilio_fiscal, ruc) {
    return axios.put(`${baseURL}usuarios/edit`,
        {
            id_usuario: id_usuario,
            id_empresa: id_empresa,
            usuario: usuario,
            numero_documento: ruc,
            clave: password,
            nombre: empresa,
            ubigeo: ubigeo,
            direccion: direccion,
            correo_electronico: correo,
            telefono: telefono,
            codigo_del_domicilio_fiscal: domicilio_fiscal
        });
}